import React, { useEffect, useState } from 'react';
import Container from 'react-bootstrap/Container';
import moment from 'moment';
import { API } from 'aws-amplify';

const providerId = 'e986bca0-ff41-11ea-8196-af25e422bd08';
const facilityId = 'ff1694c0-ffa8-11ea-bfbc-4f270cb046f3';

export default function BookingList() {
  const [subscriptions, setSubscriptions] = useState(undefined);

  useEffect(() => {
    if (!subscriptions) {
      API.get('limelockers', `/${providerId}/${facilityId}/subscriptions`)
        .then(response => {
          if (response.status && response.subscriptions) {
            setSubscriptions(response.subscriptions);
          }
        })
        .catch(console.log)
      API.get('limelockers', `/${providerId}/${facilityId}/catalog`)
        .then(console.log)
        .catch(console.log)
    }
  });
  
  return (
    <Container id="bookings">
      {
        (subscriptions && subscriptions.total_count)
          ? (
              <>
                <ul>
                  {
                    subscriptions.data.map(subscription => (
                      <li key={subscription.id}>
                        subscription: {subscription.id}<br />
                        {moment(subscription.start_date * 1000).format()}
                        &nbsp;-&nbsp;
                        {moment(subscription.canceled_at * 1000).format()}
                        <ul>
                          {
                            subscription.items.data.map(subscriptionItem => (
                              <li key={subscriptionItem.id}>
                                {subscriptionItem.price.nickname.replace('corona ', '')}<br />
                                {Number.parseFloat(subscriptionItem.price.unit_amount / 100).toFixed(2)} {subscriptionItem.price.currency} / {subscriptionItem.price.recurring.interval}<br />
                              </li>
                            ))
                          }
                        </ul>
                      </li>
                    ))
                  }
                </ul>
              </>
            )
          : null
      }
      <pre>
        {JSON.stringify(subscriptions, null, 2)}
      </pre>
    </Container>
  );
}


